import * as React from "react";
import { Link } from "gatsby";
import { TECHNOLOGY } from "@data";

const TechnologyComponent = () => {
    const TechnologyExpert = Object.values(TECHNOLOGY.value).filter(
        (t) => t.expertise
    );
    const TechnologyLearning = Object.values(TECHNOLOGY.value).filter(
        (t) => !t.expertise
    );

    return (
        <div className="content-section2 section-padding">
            <div className="container">
                <div className="row justify-content-center">
                    <div className="col-lg-9">
                        <div className="section-title text-center mb--50 mb-lg--80">
                            <h2 className="title">
                                {TECHNOLOGY.label}
                                {/* TechnologyComponent <br /> &amp; Expertise. */}
                            </h2>
                        </div>
                    </div>

                    <div className="col-lg-9 mx-auto">
                        {TechnologyExpert.map((technology, index) => (
                            <TechnologyBlock
                                technology={technology}
                                key={index}
                            />
                        ))}
                        <div className="my-5">Learning</div>
                        {TechnologyLearning.map((technology, index) => (
                            <TechnologyBlock
                                technology={technology}
                                key={index}
                            />
                        ))}
                    </div>
                </div>

                {/* <div className="h3 text-center">And many more...</div> */}
            </div>
        </div>
    );
};

export default TechnologyComponent;

const TechnologyBlock = ({ technology }) => (
    <div className="d-inline-block m-5 text-center">
        <div className="mb-2">{technology.svg && <technology.svg />}</div>
        <div>
            <small>{technology.label}</small>
        </div>
    </div>
);
